
body {
    background-color: #ebebeb;
}
#app {
    padding-left: 25%;
    padding-right: 25%;
}

.header-logo {
    float: left;
}
.header-logo img {
    width: 100px;
}
.header-menu {
    float: right;
}
.header-menu li {
    list-style-type: none;
    display: inline;
}
.header-menu li::after {
    content: '|';
    padding-left: 10px;
    padding-right: 3px;
    color: #cacaca;
}
.header-menu li:last-child::after {
    content: '';
}
a:link, a:hover, a:visited {
    text-decoration: none;
    color: black;
}

.footer {
    margin: 0 auto;
    margin-top: 40px;
    padding-top: 30px;
    padding-left: 100px;
    height: 150px;
}

.body-row {
    clear: both;
    margin: 0 auto;
    padding-top: 30px;
    padding-left: 100px;
    height: 900px;
    width: 50%;
    background-color: white;
    -webkit-box-shadow: 3px 3px 3px 3px #d0d0d0;
            box-shadow: 3px 3px 3px 3px #d0d0d0;
}
h1 {
    padding-bottom: 50px;
}
.body-btn-box {
    padding-right: 80px;
    text-align: right;
}
.body-btn-box button {
    height: 35px;
    width: 80px;
    border: none;
    border-radius: 5px;
    background: #33c197;
    color: white;
}
.tb-header {
    width: 85%;
    border-bottom: solid 1px black;
}
.tb-header p {
    display: inline-block;
}
.tb-header p:nth-child(1) {
    width: 10%;
}
.tb-header p:nth-child(2) {
    width: 50%;
}
.tb-header p:nth-child(3) {
    width: 10%;
}

.join-row {
    clear: both;
    padding-top: 30px;
}
.join-form {
    margin: 0 auto;
    padding-top: 30px;
    padding-left: 100px;
    height: 400px;
    width: 50%;
    background-color: white;
    -webkit-box-shadow: 3px 3px 3px 3px #d0d0d0;
            box-shadow: 3px 3px 3px 3px #d0d0d0;
}
h1 {
    padding-bottom: 50px;
}
.join-form input {
    border-style: none;
    border-bottom: solid black 1px;
    height: 30px;
    width: 500px;
    outline: none;
    padding-left: 10px;
}
p:last-child {
    text-align: right;
    padding-right: 150px;
}
.join-form button {
    height: 35px;
    width: 80px;
    border: none;
    border-radius: 5px;
}
.join-form button:first-child {
    background: #33c197;
    color: white;
}

